<template>
  <div id="app" @mouseover="overTimeOperation">
    <router-view />
  </div>
</template>

<script>

import {getRequest} from './api/api'

export default {
  name: 'App',
  // provide(){
  //   return{
  //     reload: this.reload
  //   }
  // },
  data(){
    return {
      //
      timmer: null,
      //isRouterAlive:true
    }
  },
  methods: {
    overTimeOperation() {
      //30
      if( this.$route.name !== ''){
        //30
        clearTimeout(this.timmer);
        var _this = this;
        this.timmer = setTimeout(()=>{
        sessionStorage.clear();
        _this.$alert('','',{
          confirmButtonText: '',
          type: 'warning',
          callback: action => {
            getRequest('/logout');
            _this.$router.push({path:"/"});
          }
        })
        
        },30*60*1000);
      }
    },
    // reload(){
    //   this.isRouterAlive = false
    //   this.$nextTick(function(){
    //     this.isRouterAlive = true
    //   })
    // }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



